body {
  background-color: #e7e7e7;
}

.container {
  border: 1px solid rgb(203 213 225);
  position: relative;
  margin: auto;
  margin-top: 30px;
  background-color: white;
  border-radius: 15px;
  box-shadow: 1px 1px 20px 4px #b5b5b5;
  height: 500px;
  width: 800px;
}
.btnAdd{
  display:block;
  margin: auto;
  margin-top: 30px;
  padding: 8px 16px;
  background: #36b3f6;
  color: white;
  font-weight: bold;
  padding: 10px 50px;
  border-radius: 5px;
  border: 0px;
  box-shadow: 1px 1px 20px 4px #b5b5b5;
}
.draggable {
  background: #36b3f6;
  border: 1px solid #ffffffca;
  border-radius: 50%;
  height: 120px;
  width: 120px;
  position: absolute;
  top: 0;
  left: 0;
  cursor: move;
  user-select: none;
  touch-action: none;
}
